import Helper from '@/function.js';
import loadMore from '../../components/load-more.vue';
export default {

	data() {
		const currentDate = this.getDate({
			format: true
		})
		return {
			token: '',
			/* type:'', */
			pageIndex: 1,
			pageSize: 10,
			rewardList: [],
			ismask2: false,
			sendType: 0,
			date: currentDate,
			isdown: true,
			sortnum: 0,
			sortlist: [{
					title: '全部',
					type: -1
				},
				{
					title: '后台充值',
					type: 1
				},
				{
					title: '任务奖励',
					type: 5
				},
				{
					title: '任务分佣',
					type: 10
				},
				{
					title: '套餐提成',
					type: 15
				},
				{
					title: '充值消费',
					type: 105
				},
				{
					title: '提现',
					type: 101
				},
				{
					title: '提现驳回',
					type: 20
				},
			]
		}
	},
	onReachBottom:function(){
		if (this.rewardList.length == this.total)
		{
			return;
			
		}
		
		this.pageIndex++;
		this.getMoneyRecordList();
	},
	onPullDownRefresh:function(){
		this.pageIndex = 1;
		this.rewardList = [];
		this.getMoneyRecordList();
	},

	onShow: function() {
		this.pageIndex = 1;
		this.rewardList = [];
		this.token = uni.getStorageSync('token');
		this.userInfo = uni.getStorageSync('userInfo');
		this.getMoneyRecordList();
	},
	methods: {
		getDate(type) {
			const date = new Date();
			let year = date.getFullYear();
			let month = date.getMonth() + 1;
			// let day = date.getDate();

			// if (type === 'start') {
			// 	year = year - 60;
			// } else if (type === 'end') {
			// 	year = year + 2;
			// }
			month = month > 9 ? month : '0' + month;;
			// day = day > 9 ? day : '0' + day;
			return `${year}-${month}`;
		},
		bindDateChange: function(e) {
			this.date = e.target.value
		},


		navClick(index) {
			this.isdown = false;
			this.ismask2 = false;
			this.getMoneyRecordList()();
		},
		closeallmask() {
			this.ismask2 = false;

		},
		downselectsort(index) {
			this.isdown = true;
			this.ismask2 = false;
			this.sortnum = index;
			this.pageIndex = 1;
			this.rewardList = [];
			this.getMoneyRecordList();
		},
		openmask2() {
			this.ismask = false;
			console.log('ismaskkkkkkkkkkkkkkk' + this.ismask);
			if (this.ismask2 == false) {
				this.ismask2 = true;
			} else {
				this.ismask2 = false;
			}

		},
		/* 调用资金明细接口 */
		getMoneyRecordList() {
			var _this = this
			var params = {
				pageIndex: _this.pageIndex,
				pageSize: _this.pageSize,

			};
			uni.request({
				url: _this.ApiGateWay + '/sale/Member/GetCreditRecordList',
				data: params,
				method: 'POST',
				dataType: 'json',
				header: {
					Authorization: _this.token
				},
				success: function(res) {
					console.log(res + '111');
					if (Helper.CheckData(res)) {
						let {
							errorCode,
							errorMessage,
							list,
							total,
							totalPage
						} = res;
						if (list == null) {

						}
						_this.total = res.data.total;
						_this.rewardList = _this.rewardList.concat(res.data.list);

					} else {
						_this.total = res.data.total;
					}


				},

				fail: function() {
					uni.hideLoading();
				},
				complete: function() {
					uni.stopPullDownRefresh()
					uni.hideLoading();
				}
			});



		}




	}

}
